.container{margin:0 auto;padding:2rem 1rem;text-align:center;border-radius:12px}.statsGrid{display:flex;justify-content:center;gap:1.5rem}.statItem{width:240px;min-height:220px;display:flex;flex-direction:column;align-items:center;justify-content:center;border-radius:12px;box-shadow:0 8px 15px rgba(0,0,0,.1);transition:transform .3s ease,box-shadow .3s ease;color:#333;text-align:center}.statItem:hover{transform:translateY(-10px);box-shadow:0 12px 25px rgba(0,0,0,.2)}.icon{font-size:3rem;color:#ff5722;margin-bottom:1rem}.value{font-size:2.5rem;font-weight:bold;color:#007bff;margin-bottom:.5rem}.label{font-size:1rem;color:#555}.loading,.error{font-size:1.5rem;color:#666;margin-top:2rem}@media(max-width: 992px){.container{margin:0 auto;padding:2rem 1rem;text-align:center;border-radius:12px}.statsGrid{display:flex;justify-content:center;gap:1.2rem}.statItem{width:170px;min-height:190px}.icon{font-size:38px}.value{font-size:2.2rem}.label{font-size:1rem;color:#555}}@media(max-width: 768px){.container{margin:0 auto;padding:2rem 1rem;text-align:center;border-radius:12px}.statsGrid{display:flex;justify-content:center;flex-wrap:wrap;gap:1rem}.statItem{width:160px;min-height:180px}.icon{font-size:35px}.value{font-size:1.8rem}.label{font-size:1rem;color:#555}}@media(max-width: 428px){.container{margin:0 auto;padding:2rem 1rem;text-align:center;border-radius:12px}.statsGrid{display:flex;justify-content:center;flex-wrap:wrap;gap:1rem}.statItem{width:130px;min-height:140px}.icon{font-size:28px}.value{font-size:1.2rem}.label{font-size:14px;color:#555}}